.App {
  text-align: center;
  padding: 20px;
  max-width: 800px;
  margin: 0 auto;
  font-family: Arial, sans-serif;
}

.todo-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

h1 {
  font-size: 2rem;
  margin-bottom: 20px;
}

.todo-input {
  display: flex;
  margin-bottom: 20px;
}

.todo-input input {
  padding: 10px;
  margin-right: 10px;
  border: 2px solid #ccc;
  border-radius: 5px;
}

.todo-input button {
  padding: 10px 15px;
  background-color: #36A2EB;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.todo-input button:hover {
  background-color: #2a8ed6;
}

.todo-list {
  width: 60%;
}

.todo-list ul {
  list-style-type: none;
  padding: 0;
}

.todo-list li {
  padding: 15px;
  margin-bottom: 10px;
  background-color: #ebe1e1;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

